import React from 'react'
import { StyleDefinition } from './types'
import { Switch } from 'antd'

type Direction = 'row' | 'column'
const FlexDirection: StyleDefinition<Direction> = {
  title: '',
  defaultValue: 'row',
  description: '',
  Blueprint (props: { value: Direction; onChange: (value: Direction) => void }) {
    return (
      <Switch checked={props.value === 'row'} checkedChildren="" unCheckedChildren="" onChange={(v) => props.onChange(v ? 'row' : 'column')} />
    )
  },
  render (value: Direction) {
    return { flexDirection: value }
  },
  key: 'FlexDirection'
}
export default FlexDirection
