import React from 'react'
import { StyleDefinition } from './types'
import { rem } from './utils'
import { InputNumber, Select } from 'antd'
import styled from 'styled-components'
import Color from './color'

const Container = styled.div`
  flex: auto;
`

const ItemsContainer = styled.div`
  display: flex;

  > div:last-child {
    width: 58px;

    > div:nth-child(2) {
      left: -212px;
      top: -279px;
    }
  }
`

type LineStyle = 'solid' | 'double' | 'dotted'
interface BlockStyle {
  size: number,
  style: LineStyle,
  color: string
}

interface Value {
  top: BlockStyle
  bottom: BlockStyle
  left: BlockStyle
  right: BlockStyle
}

const StyleSelect = (props: { value: LineStyle, onChange: (value: LineStyle) => void }) => (
  <Select value={props.value} onChange={props.onChange}>
    <Select.Option value="solid"></Select.Option>
    <Select.Option value="double"></Select.Option>
    <Select.Option value="dotted"></Select.Option>
  </Select>
)

const Items = (props: { title: string, value: BlockStyle, onChange: (value: BlockStyle) => void }) => (
  <ItemsContainer>
    <InputNumber
      addonBefore={props.title}
      min="0"
      max="999"
      style={{ width: 190 }}
      value={String(props.value.size)}
      onChange={(v) => props.onChange({ ...props.value, size: Number(v) })}
      addonAfter={<StyleSelect value={props.value.style} onChange={(v) => props.onChange({ ...props.value, style: v })} />}
    />
    <Color.Blueprint value={props.value.color} onChange={v => props.onChange({ ...props.value, color: v })} />
  </ItemsContainer>
)
const Border: StyleDefinition<Value> = {
  Blueprint (props: { value: Value; onChange: (value: Value) => void }) {
    return (
      <Container>
        <Items title="" value={props.value.top} onChange={(v) => props.onChange({ ...props.value, top: v })} />
        <Items title="" value={props.value.bottom} onChange={(v) => props.onChange({ ...props.value, bottom: v })} />
        <Items title="" value={props.value.left} onChange={(v) => props.onChange({ ...props.value, left: v })} />
        <Items title="" value={props.value.right} onChange={(v) => props.onChange({ ...props.value, right: v })} />
      </Container>
    )
  },
  render (value: Value) {
    return {
      borderTop: `${rem(value.top.size)} ${value.top.style} ${value.top.color}`,
      borderBottom: `${rem(value.bottom.size)} ${value.bottom.style} ${value.bottom.color}`,
      borderLeft: `${rem(value.left.size)} ${value.left.style} ${value.left.color}`,
      borderRight: `${rem(value.right.size)} ${value.right.style} ${value.right.color}`
    }
  },
  defaultValue: {
    bottom: { size: 0, color: '#000', style: 'solid' },
    left: { size: 0, color: '#000', style: 'solid' },
    right: { size: 0, color: '#000', style: 'solid' },
    top: { size: 0, color: '#000', style: 'solid' }
  },
  description: '',
  title: '',
  key: 'Border'
}

export default Border
